radius: 8px;\n    overflow: hidden;\n\n    transition: max-height 0.5s ease, opacity 0.7s ease;\n\n    &--hidden {\n      max-height: 0;\n      opacity: 0%;\n    }\n  }\n\n  &__item {\n    width: 100%;\n    padding: 6px 8px 4px 8px;\n\n    cursor: pointer;\n\n    &:hover {\n      background-color: #ddd;\n    }\n\n    &:not(:last-child) {\n      border-bottom: 2px solid #00000011;\n    }\n  }\n}\n\n.carousel {\n  position: relative;\n  width: fit-content;\n\n  .dropdown {\n    position: absolute;\n    top: 8px;\n    right: 8px;\n\n    img {\n      filter: invert(1);\n    }\n  }\n\n  &__slides-container {\n    width: 500px;\n    border-radius: 12px;\n    overflow: hidden;\n    @include box-shadow(#00000022);\n  }\n\n  &__slide {\n    display: none;\n\n    width: 100%;\n    height: 100%;\n\n    &--active {\n      display: block;\n    }\n  }\n\n  &__nav {\n    @include button(32px, 32px, 80%, #00000022);\n    border-radius: 50%;\n\n    position: absolute;\n    top: 50%;\n    transform: translateY(-50%);\n\n    &--left {\n      left: 8px;\n    }\n\n    &--right {\n      right: 8px;\n    }\n\n    img {\n      filter: invert(1);\n    }\n  }\n\n  &__circles-container {\n    @include flex-container();\n    gap: 8px;\n\n    position: absolute;\n    bottom: 12px;\n    left: 50%;\n    transform: translateX(-50%);\n  }\n\n  &__circle {\n    height: 16px;\n    width: 16px;\n\n    border: #ffffff 2px solid;\n    border-radius: 50%;\n    opacity: 50%;\n\n    cursor: pointer;\n    transition: opacity 0.3s ease, background-color 0.3s ease;\n\n    &--active {\n      opacity: 70%;\n      background-color: #ffffff;\n    }\n  }\n}"],"sourceRoot":""}]);
